import { StaggeredContainer, StaggeredItem } from "../../animations/staggeredFadeIn";
import { Header } from "../styled";
import { List, Tile, Name, Description, ListWrapper } from "./styled";
import { Link } from "react-scroll";

const Offer = () => {
    return (
        <ListWrapper id="usugi">
            <StaggeredContainer>
                <StaggeredItem>
                    <Header>Nasza oferta</Header>
                </StaggeredItem>
                <StaggeredItem>
                    <List>
                        <Tile as={Link} to="rejsy-po-jeziorze" smooth={true} duration={600} offset={-90} style={{ cursor: 'pointer' }}>
                            <Name>Rejsy po jeziorze Czorsztyskim</Name>
                            <Description>Przyroda w zasigu rki! Wyjtkowe gondole przewioz Ci po Jeziorze Czorsztyskim</Description>
                        </Tile>
                        <Tile as={Link} to="domki" smooth={true} duration={1000} offset={-90} style={{ cursor: 'pointer' }}>
                            <Name>Domki "Zamkowe wzgrze"</Name>
                            <Description>Wynajem dwch domkw ulokowanych na brzegu Jeziora Czorsztyskiego, tu przy Zamku w Niedzicy</Description>
                        </Tile>
                        <Tile as={Link} to="rowery" smooth={true} duration={1200} offset={-90} style={{ cursor: 'pointer' }}>
                            <Name>Wypoyczalnia rowerw "Velo Czorsztyn"</Name>
                            <Description>Wypoyczalnia rowerw tradycyjnych, elektrycznych oraz sprztu rowerowego znajdujca si przy naszej przystani w Czorsztynie</Description>
                        </Tile>
                    </List>
                </StaggeredItem>
            </StaggeredContainer>
        </ListWrapper>
    );
};

export default Offer;