import { useShowMore } from "../../../hooks/useShowMore";
import { BoldText, ButtonDiv, CruiseSubHeader, CruiseWrapper, Description, ListItem, ListTitle, ShowMoreButton, ShowMoreContent, StyledList } from "../styled";
import { StaggeredContainer, StaggeredItem } from "../../animations/staggeredFadeIn";


const Houses = () => {

    const { showAll, handleToggle, buttonRef } = useShowMore();

    return (
        <CruiseWrapper>
            <StaggeredContainer>
                <ShowMoreContent $isVisible={showAll}>
                    <StaggeredItem>
                        <CruiseSubHeader>Wyposaenie i udogodnienia Domkw</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Kady z naszych dwch domkw, o przytulnym metrau 35 m, zosta zaprojektowany tak, aby zapewni maksymalny komfort i funkcjonalno dla naszych Goci. To w peni umeblowane i nowoczenie wyposaone przestrzenie, gotowe na przyjcie do 6 osb.
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <ListTitle>Kady domek oferuje:</ListTitle>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText>Przestronny Salon z aneksem kuchennym: </BoldText>
                                Serce domku, idealne miejsce na wsplne posiki i wieczorne rozmowy. Znajdziesz tu wygodn cz wypoczynkow oraz telewizor, zapewniajcy rozrywk po dniu penym wrae.
                            </ListItem>
                            <ListItem>
                                <BoldText>W peni wyposaon Kuchni: </BoldText>
                                Gotowanie stanie si przyjemnoci dziki kompletnemu wyposaeniu: lodwce, mikrofalwce, pycie indukcyjnej, czajnikowi, a take wszelkim niezbdnym naczyniom do gotowania i penej zastawie stoowej dla 6 osb.
                            </ListItem>
                            <ListItem>
                                <BoldText>Dwie Przytulne Sypialnie: </BoldText>
                                Idealne na odpoczynek po dniu penym przygd, zapewniajce komfortowy sen.
                            </ListItem>
                            <ListItem>
                                <BoldText>Nowoczesn azienk z Prysznicem: </BoldText>
                                Estetyczna i funkcjonalna, z dbaoci o kady detal.
                            </ListItem>
                            <ListItem>
                                <BoldText>Klimatyzacj: </BoldText>
                                Gwarantuje przyjemn temperatur nawet w upalne dni, zapewniajc komfortowy wypoczynek.
                            </ListItem>
                            <ListItem>
                                <BoldText>Darmowe Wi-Fi: </BoldText>
                                Bd w kontakcie ze wiatem lub dziel si swoimi wspomnieniami z wakacji bez ogranicze.
                            </ListItem>
                            <ListItem>
                                <BoldText>Bezpatny parking: </BoldText>
                                Prywatny parking z bram znajdujcy si na ogrodzonym terenie domkw.
                            </ListItem>
                            <ListItem>
                                <BoldText>Udogodnienia: </BoldText>
                                Trampolina dla dzieci oraz miejsce do grillowania obok domku.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Twj Prywatny Taras z Widokiem</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Do kadego domku przynaley przestronny taras, wyposaony w komfortowe meble wypoczynkowe. To idealne miejsce na porann kaw z widokiem na jezioro, relaks z ksik w cigu dnia czy wieczorne biesiadowanie pod gwiazdami. Ciesz si wieym powietrzem i urokami otaczajcej przyrody, majc jednoczenie prywatn przestrze tylko dla siebie.
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Idealna Lokalizacja</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Domki "Zamkowe Wzgrze" to nie tylko komfortowe wntrza, ale przede wszystkim niezrwnana lokalizacja. Znajduj si na samym brzegu Jeziora Czorsztyskiego, co gwarantuje atwy dostp do wody i moliwo podziwiania spektakularnych widokw. Bezporednie ssiedztwo Zamku w Niedzicy, play przy jeziorze oraz przystani gondoli sprawia, e natura, historia oraz atrakcje jeziora s na wycignicie rki. To doskonaa baza wypadowa do zwiedzania Pienin, spywu Dunajcem, pieszych wdrwek czy wycieczek rowerowych.
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Atrakcje w okolicy</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <ListTitle>Najpopularniejsze atrakcje w okolicy domkw i jeziora Czorsztyskiego:</ListTitle>
                        <StyledList>
                            <ListItem>
                                <BoldText>Jezioro Czorsztyskie: </BoldText>
                                malowniczy zbiornik wodny, powstay na Dunajcu, idealny do aktywnego wypoczynku i relaksu.
                            </ListItem>
                            <ListItem>
                                <BoldText>Zamek Dunajec w Niedzicy: </BoldText>
                                wietnie zachowana, XIV-wieczna warownia, z ktr wie si wiele legend np. o Inkach i skarbie, Biaej Damie.
                            </ListItem>
                            <ListItem>
                                <BoldText>Zapora Wodna w Niedzicy: </BoldText>
                                Imponujca konstrukcja, zapora, ktra spitrza wody Dunajca i tworzy Jezioro Czorsztyskie.
                            </ListItem>
                            <ListItem>
                                <BoldText>Zamek w Czorsztynie: </BoldText>
                                malownicze ruiny krlewskiego zamku, pooone na wzgrzu po drugiej stronie Jeziora Czorsztyskiego.
                            </ListItem>
                            <ListItem>
                                <BoldText>Trasa rowerowa Velo Czorsztyn: </BoldText>
                                pikna, 28 kilometrowa trasa rowerowa wok Jeziora Czorsztyskiego czca oba zamki.
                            </ListItem>
                            <ListItem>
                                <BoldText>Rejsy Gondolami: </BoldText>
                                widokowe rejsy midzy zamkami. To fantastyczny sposb na podziwianie obu zamkw i panoramy Pienin z wody.
                            </ListItem>
                            <ListItem>
                                <BoldText>Wypoyczalnie rowerw: </BoldText>
                                punkty umoliwiajce wypoyczenie roweru i pokonanie malowniczych tras rowerowych w okolicy.
                            </ListItem>
                            <ListItem>
                                <BoldText>Plaa Pieniny: </BoldText>
                                piaszczysta plaa i strzeone kpielisko, wietne na letni relaks i kpiele, zlokalizowane u podna zamku.
                            </ListItem>
                            <ListItem>
                                <BoldText>Wypoyczalnie sprztu wodnego: </BoldText>
                                Kajaki, rowerki wodne, dki  idealne do samodzielnego odkrywania jeziora.
                            </ListItem>
                            <ListItem>
                                <BoldText>Spyw Przeomem Dunajca: </BoldText>
                                jedna z najbardziej znanych atrakcji Pienin, spyw tradycyjnymi drewnianymi tratwami przez malowniczy przeom Dunajca.
                            </ListItem>
                            <ListItem>
                                <BoldText> Piesze szlaki turystyczne: </BoldText>
                                Trzy Korony, Sokolica, Polana Ciswka, Gra Wdar i wiele innych przepiknych miejsc z ktrych mona podziwia roztaczajc si panoram jeziora, Tatr i Pienin.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                </ShowMoreContent>

                <ButtonDiv>
                    <ShowMoreButton onClick={handleToggle} ref={buttonRef}>
                        {showAll ? "Zwi opis" : "Poka peny opis"}
                    </ShowMoreButton>
                </ButtonDiv>

            </StaggeredContainer>
        </CruiseWrapper>
    );
};

export default Houses;