import { useShowMore } from "../../../hooks/useShowMore";
import { StaggeredContainer, StaggeredItem } from "../../animations/staggeredFadeIn";
import { BoldText, ButtonDiv, CruiseSubHeader, CruiseWrapper, Description, ListItem, ShowMoreButton, ShowMoreContent, StyledList } from "../styled";


const Bikes = () => {

    const { showAll, handleToggle, buttonRef } = useShowMore();

    return (
        <CruiseWrapper>
            <StaggeredContainer>
                <ShowMoreContent $isVisible={showAll}>
                    <StaggeredItem>
                        <CruiseSubHeader>Szeroka Oferta i Komfortowy Sprzt</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            W naszej wypoyczalni czeka na Ciebie bogaty wybr sprawdzonych, regularnie serwisowanych rowerw, idealnych zarwno dla pocztkujcych, jak i dowiadczonych cyklistw:
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText>Rowery tradycyjne: </BoldText>
                                Lekkie, wygodne i gotowe do jazdy po trasie Velo Czorsztyn i pobliskich szlakach. Doskonae dla tych, ktrzy ceni klasyk i chc o wasnych siach pokona kade wzniesienie.
                            </ListItem>
                            <ListItem>
                                <BoldText>Rowery elektryczne: </BoldText>
                                Dla tych, ktrzy ceni sobie komfort i chc bez wysiku pokonywa dusze dystanse lub wymagajce podjazdy, cieszc si widokami bez nadmiernego zmczenia.
                            </ListItem>
                            <ListItem>
                                <BoldText>Rowery dziecice i sprzt dodatkowy: </BoldText>
                                Mamy wszystko, by caa rodzina moga bezpiecznie ruszy w tras. Oferujemy rowery dziecice, foteliki, przyczepki rowerowe oraz kaski w rnych rozmiarach.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Wypoyczenie sprztu odbywa si szybko i sprawnie  nasi pracownicy chtnie doradz w wyborze najlepszego roweru i odpowiedniego rozmiaru, a take podpowiedz, ktr tras wybra oraz podziel si przydatnymi wskazwkami, ktre pomog w peni cieszy si rowerow przygod i odkrywa najpikniejsze zaktki Jeziora Czorsztyskiego i Pienin!
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Przeprawa Gondol  Wygoda i Oszczdno Czasu</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Wypoyczajc rower w naszej wypoyczalni, zyskujesz <BoldText>darmowy przewz sprztu na pokadzie gondoli</BoldText>, kursujcej midzy zamkami w Czorsztynie i Niedzicy. To idealne rozwizanie, ktre znacznie uatwia logistyk i pozwala na:
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText>Wykonanie penej ptli malownicz tras Velo Czorsztyn </BoldText>
                                bez koniecznoci powrotu t sam drog lub pokonywania dodatkowych 12 kilometrw poboczem drogi aby zamkn ptl.
                            </ListItem>
                            <ListItem>
                                <BoldText>Poczenie dwch kluczowych punktw Velo Czorsztyn</BoldText>
                                , umoliwiajc komfortowe rozpoczcie lub kontynuowanie trasy po obu stronach jeziora.
                            </ListItem>
                            <ListItem>
                                <BoldText>Poczenie wycieczki rowerowej ze zwiedzaniem zamkw </BoldText>
                                po obu stronach jeziora i odpoczynkiem nad wod.
                            </ListItem>
                            <ListItem>
                                <BoldText>Oszczdno czasu i energii </BoldText>
                                dziki czemu moesz w peni skupi si na czerpaniu radoci z jazdy i otaczajcych widokw.
                            </ListItem>
                            <ListItem>
                                <BoldText>Dodatkowe urozmaicenie wycieczki</BoldText>
                                , wprowadzajc element wodnej przygody i oferujc wyjtkowe widoki na zamki oraz okolic z perspektywy jeziora.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Dla Kogo?</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Nasza wypoyczalnia to idealna opcja dla kadego, kto chce aktywnie spdzi czas nad Jeziorem Czorsztyskim:
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText>Dla rodzin z dziemi: </BoldText>
                                Bezpieczne rowery i akcesoria dla najmodszych gwarantuj udan wspln przygod.
                            </ListItem>
                            <ListItem>
                                <BoldText>Dla turystw pieszych: </BoldText>
                                Jeli chcesz odkry region z innej perspektywy i zobaczy wicej w krtszym czasie.
                            </ListItem>
                            <ListItem>
                                <BoldText>Dla mionikw natury: </BoldText>
                                Pocz ruch na wieym powietrzu z podziwianiem zapierajcych dech w piersiach krajobrazw Pienin i Jeziora Czorsztyskiego.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            <BoldText>Odkryj region w aktywny sposb z wypoyczalni Velo Czorsztyn!</BoldText> Ciesz si swobod jazdy, zapierajcymi dech w piersiach widokami i niezapomnian przygod na dwch kkach. Serdecznie zapraszamy!
                        </Description>
                    </StaggeredItem>
                </ShowMoreContent>

                <ButtonDiv>
                    <ShowMoreButton onClick={handleToggle} ref={buttonRef}>
                        {showAll ? "Zwi opis" : "Poka peny opis"}
                    </ShowMoreButton>
                </ButtonDiv>
            </StaggeredContainer>
        </CruiseWrapper>
    );
};

export default Bikes;