import { Link } from "react-scroll";
import { useShowMore } from "../../../hooks/useShowMore";
import { BoldText, ButtonDiv, CruiseSubHeader, CruiseWrapper, Description, ListItem, ListTitle, ShowMoreButton, ShowMoreContent, SocialLink, StyledList } from "../styled";
import { StaggeredContainer, StaggeredItem } from "../../animations/staggeredFadeIn";

const Cruises = () => {

    const { showAll, handleToggle, buttonRef } = useShowMore();

    return (
        <CruiseWrapper>
            <StaggeredContainer>
                <ShowMoreContent $isVisible={showAll}>
                    <StaggeredItem>
                        <CruiseSubHeader>Dostpne trasy rejsw</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <ListTitle>Przeprawa pomidzy Zamkami w Czorsztynie i Niedzicy</ListTitle>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText> Opis: </BoldText>
                                Ta trasa to idealna okazja do swobodnego zwiedzenia okolicy na obydwu brzegach Jeziora Czorsztyskiego. Podziwiaj ruiny zamkw w Czorsztynie i Niedzicy, przemieszczajc si komfortowo midzy nimi.
                            </ListItem>
                            <ListItem>
                                <BoldText>Czstotliwo: </BoldText>
                                Kursy stae, odbywaj si co 15 minut.
                            </ListItem>
                            <ListItem>
                                <BoldText> Punkty startowe: </BoldText>
                                Z Czorsztyna lub Niedzicy.
                            </ListItem>
                            <ListItem>
                                <BoldText>Dla kogo: </BoldText>
                                Idealna opcja dla tych, ktrzy chc poczy rejs z pieszym zwiedzaniem zamkw lub rowerzystw ktrzy chc skorzysta z atwiejszego wariantu zrobienia ptli dookoa jeziora ciek Velo Czorsztyn i przeprawi si na drugi brzeg z rowerem
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <ListTitle>Rejs widokowy dookoa Jeziora Czorsztyskiego</ListTitle>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText> Opis: </BoldText>
                                Rejs dookoa Jeziora Czorsztyskiego to wyjtkowa okazja do zapoznania si z zamkami, zapor oraz malowniczymi zatoczkami i urokliwymi miejscami z perspektywy wody. Ciesz si widokami i spokojem natury.
                            </ListItem>
                            <ListItem>
                                <BoldText>Czas trwania: </BoldText>
                                Okoo 40 minut.
                            </ListItem>
                            <ListItem>
                                <BoldText> Wymagania: </BoldText>
                                Rejs moe si odby przy minimum 8 osobach na pokadzie gondoli, po wczeniejszym uzgodnieniu.
                            </ListItem>
                            <ListItem>
                                <BoldText>Wskazwka: </BoldText>
                                W sezonie (1 lipca - 31 sierpnia) rejsy mog odbywa si w godzinach porannych (9:00-10:00) i popoudniowych (17:00-18:00). Ze wzgldu na wymagane minimum osb, prosimy o wczeniejszy kontakt telefoniczny w celu uzgodnienia szczegw.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Godziny Kursowania Gondoli</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Nasze gondole "Czorsztynianki" kursuj codziennie, zapewniajc elastyczno planowania Twojej wycieczki.
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText>Sezon </BoldText>
                                (od 1 lipca do 31 sierpnia): 9:00  18:00
                            </ListItem>
                            <ListItem>
                                <BoldText>Poza sezonem </BoldText>
                                (od 1 maja do 30 czerwca, od 1 wrzenia do 30 wrzenia): 10:00  16:00
                            </ListItem>
                            <ListItem>
                                <BoldText>Wane: </BoldText>
                                Ze wzgldw pogodowych godziny rejsw mog ulec zmianie. O aktualnych godzinach lub ewentualnych zmianach informujemy codziennie i na bieco na naszych <SocialLink as={Link} to="address" smooth={true} duration={1000} offset={-90} style={{ cursor: 'pointer' }}>social mediach.</SocialLink>
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Dodatkowe informacje</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText>Flota: </BoldText>
                                Dysponujemy 6 komfortowymi gondolami.
                            </ListItem>
                            <ListItem>
                                <BoldText>Pojemno: </BoldText>
                                Jednorazowo moemy zabra do 72 osb (12 osb na gondol).
                            </ListItem>
                            <ListItem>
                                <BoldText>Dla rowerzystw: </BoldText>
                                Na gondolach mona przewozi rowery oraz przyczepki rowerowe.
                            </ListItem>
                            <ListItem>
                                <BoldText>Dla pupili: </BoldText>
                                Zwierzta s mile widziane na pokadzie.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                    <StaggeredItem>
                        <CruiseSubHeader>Przysta Czorsztynianka w Czorsztynie</CruiseSubHeader>
                    </StaggeredItem>
                    <StaggeredItem>
                        <Description>
                            Po rejsie zapraszamy do naszej przystani w Czorsztynie, ktra oferuje wiele moliwoci spdzenia czasu nad urokliwym Jeziorem Czorsztyskim.
                        </Description>
                    </StaggeredItem>
                    <StaggeredItem>
                        <StyledList>
                            <ListItem>
                                <BoldText>Maa gastronomia: </BoldText>
                                Dostpne s zimne i ciepe napoje, przekski, lody oraz sodycze.
                            </ListItem>
                            <ListItem>
                                <BoldText>Strefa relaksu: </BoldText>
                                Do dyspozycji goci oddajemy urokliwy ogrdek z awkami, parasolami chronicymi przed socem i deszczem, wygodne pufy i leaki, a take hamaki w zacienionym zagajniku. Idealne miejsce na odpoczynek po rejsie lub w przerwie podczas pokonywania trasy rowerowej dookoa jeziora.
                            </ListItem>
                        </StyledList>
                    </StaggeredItem>
                </ShowMoreContent>

                <ButtonDiv>
                    <ShowMoreButton onClick={handleToggle} ref={buttonRef}>
                        {showAll ? "Zwi opis" : "Poka peny opis"}
                    </ShowMoreButton>
                </ButtonDiv>
            </StaggeredContainer>
        </CruiseWrapper >
    );
};

export default Cruises;